import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { UserService } from 'src/app/services/user.service';


@Component({
  selector: 'app-hidratar-form',
  templateUrl: 'hidratar-form.page.html',
  styleUrls: ['hidratar-form.page.scss']
})
export class HidratarFormPage implements OnInit {
  form: FormGroup;

  constructor(private route: Router, private userSrv: UserService) {}

  ngOnInit() {
    this.initialForm();
  }

  initialForm() {
    this.form = new FormGroup({
      target: new FormControl(null, [Validators.required]),
    });
  }

  async submit() {
    try {
      console.log(this.form.value);
      await this.userSrv.insertHidroUser(this.form.value).toPromise(); 
      this.back()
    } catch (error) {
      console.error('Erro ao enviar o formulário:', error);
    }
  }

  loadAsyncData() {
    // Implemente aqui a lógica para carregar informações assincronamente
    // Pode ser uma chamada a outro serviço ou API
  }

  back() {
    this.route.navigate(['/tabs/tab2']);
  }
}
